
.autoRenew .autoRenewList {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0.25rem 0.46875rem;
  background-color: #fff;
}
.autoRenew .autoRenewList p {
  margin-top: 0.25rem;
  overflow: hidden;
}
.autoRenew .autoRenewList p .icon {
  font-size: 0.5rem;
  float: left;
}
.autoRenew .remindItem {
  border-top: 1px solid #e5e5e5;
}
.autoRenew .remindItem .downIcon {
  float: right !important;
}
.autoRenew .timeContent {
  width: 100%;
  bottom: 0;
  height: 5rem;
  position: absolute;
  background-color: #fff;
}
.autoRenew .timeContent .timeConfirm {
  display: -webkit-box;
  display: -ms-flexbox;
