  border-top: 1px solid #e5e5e5;
  height: 1.25rem;
  line-height: 1.25rem;
}
.autoRenew .timeContent .timeConfirm p {
  width: 50%;
  text-align: center;
}
.autoRenew .timeContent .picker-item {
  font-size: 0.40625rem !important;
}
.autoRenew .timeContent .changeTime {
  text-align: center;
  height: 0.9375rem;
  line-height: 0.9375rem;
  border-bottom: 1px solid #e5e5e5;
}
