
.fixSearch {
  position: fixed;
}
.fixSearch .searchTitle {
  border-bottom: 1px solid #e5e5e5;
  border-top: 1px solid #e5e5e5;
}
.searchList {
  background-color: #fff;
  width: 100%;
  top: 0rem;
  z-index: 300;
}
.searchList .searchTitle {
  padding: 0.625rem;
  padding-top: 0.375rem;
  font-size: 0.375rem;
  color: #969696;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.searchList .searchTitle li {
  float: left;
  width: 25%;
  text-align: center;
}
.searchList .searchTitle li .iconList {
  font-size: 0.375rem;
}
.searchList .searchDetails {
  position: absolute;
  z-index: 50;
  font-size: 0.375rem;
  width: 10rem;
  color: #969696;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.searchList .searchDetails li {
  padding: 0.3125rem 0.625rem;
  border-top: 1px solid #e5e5e5;
}
.searchList .item1 li {
  padding-left: 0.78125rem;
}
.searchList .item2 li {
  padding-left: 3.125rem;
}
.searchList .item3 li {
  padding-left: 5.625rem;
}
.searchList .item4 li {
  padding-left: 7.8125rem;
}



