
.calendar {
  margin: 0.3125rem 0rem;
  padding-left: 0.625rem;
  background-color: #fff;
  font-size: 0.375rem;
}
.calendar .title {
  text-align: left;
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 0.3125rem;
}
.calendar .calendarMain {
  margin-top: 0.46875rem;
}
.calendar .calendarMain .calendarDate .icon {
  font-family: 'iconfont';
  font-style: normal;
  font-size: 0.40625rem;
  margin-top: 0.03125rem;
}
.calendar .calendarMain .calendarDate .beforeImg {
  float: left;
}
.calendar .calendarMain .calendarDate .nextImg {
  float: right;
}
.calendar .calendarMain .calendarDate span {
  padding-right: 1.5625rem;
}
.calendar .calendarMain .calendarDate .currentDate {
  color: #3fadf9;
}
.calendar .calendarMain .calendarDate .nextDate {
  float: right;
  padding-right: 0rem;
}
.calendar .calendarMain .calendarDateItem ul {
  color: #333;
  padding: 0.3125rem 0rem;
}
.calendar .calendarMain .calendarDateItem ul li {
  float: left;
  width: 1.25rem;
  padding-top: 0.5rem;
  text-align: center;
  overflow: hidden;
  position: relative;
}
.calendar .calendarMain .calendarDateItem ul li p {
  width: 0.5625rem;
  height: 0.5625rem;
  line-height: 0.5625rem;
  font-size: 0.3125rem;
  border-radius: 50%;
  background-color: red;
  margin: 0 auto;
  margin-top: 0.375rem;
  text-align: center;
  color: #fff;
  position: absolute;
  left: 0.3125rem;
  top: 0.9375rem;
}
.calendar .calendarMain .calendarDateItem ul li .noData {
  background-color: transparent;
}
.calendar .calendarMain .calendarDateItem ul .weekNum {
  padding: 0rem;
}
.calendar .calendarMain .calendarDateItem ul .beforeDay {
  color: #999;
}



